import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
    ifDefined,
} from '@umetrics/sartorius-ui-base';
import { modalStyles } from './modalStyle';
import { ModalProperties, ModalSlot } from './modalProperties';
import { ModalSize } from './modalSize';
import cancel from '@umetrics/sartorius-ui-icon/dist/icons/cancel';
import { version } from './metadata';
import { ModalEvent } from './modalEvent';
import { focusableElements } from '@umetrics/sartorius-ui-utils';
import type { Overlay as OverlayT } from '@umetrics/sartorius-ui-overlay';

@defineElement({
    tagName: 'modal',
    styles: modalStyles,
    version,
    //    sub: [Icon, Button, Scrollbar, Overlay]
})
export class Modal extends BaseElement implements ModalProperties {
    // Static members
    private static Button;
    private static Icon;
    private static Scrollbar;
    private static Overlay;

    private static animations = {
        toggle: {
            show: [
                {
                    visibility: 'hidden',
                    scale: 0.9,
                },
                {
                    visibility: 'visible',
                    scale: 'unset',
                },
            ],
        },
    };

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    get hide() {
        return this._hide;
    }

    set hide(val) {
        const prev = this._hide;
        if (prev !== val) {
            if (val) {
                this._hide = val;
                this.requestUpdate('hide', prev);
            } else {
                const ol = this.shadowRoot!.firstElementChild as OverlayT;
                ol.hide = false;
                ol.updateComplete.then(() => {
                    this._hide = val;
                    this.requestUpdate('hide', prev);
                });
            }
        }
    }
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'no-overlay' })
    noOverlay = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'click-outside-close' })
    clickOutsideClose = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'escape-close' })
    escapeClose = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'inner-padding' })
    innerPadding = false;
    @property({ type: ModalSize, attribute: 'modal-size' })
    modalSize = ModalSize.Default;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    resizeable = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    draggable = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'fill-height' })
    fillHeight = false;
    @property({ type: Boolean, attribute: 'no-scroll', converter: booleanConverter })
    noScroll = false;
    @property({ type: Boolean, attribute: 'fixed-width', converter: booleanConverter })
    fixedWidth;

    // Protected members
    private _focusableElements: HTMLElement[] | null = null;
    private _unobserveChildChanges!: () => void;
    @state()
    private _dragging = false;
    private _dragPos: { x: number; y: number } = { x: 0, y: 0 };
    private _hide = false; // Should hide/show modal
    private _rendered = false; //Flag for first rendered
    // Private members
    @query(`[class*="-root"]`)
    private _root;
    @query('div[class*="-inner"]')
    private _inner;

    constructor() {
        super();

        this._closeClickOutside = this._closeClickOutside.bind(this);
        this._keyDownListener = this._keyDownListener.bind(this);
        this._elementDrag = this._elementDrag.bind(this);
        this._closeDragElement = this._closeDragElement.bind(this);
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-scrollbar'), elem: ['Scrollbar'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-overlay'), elem: ['Overlay'] });

        Modal.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });

        this._unobserveChildChanges = this._observeChildSubtree();
    }

    disconnectedCallback() {
        document.removeEventListener('keydown', this._keyDownListener);
        if (this._unobserveChildChanges) {
            this._unobserveChildChanges();
        }
        super.disconnectedCallback();
    }

    // Before render
    protected update(changedProperties) {
        super.update(changedProperties);

        this._resetFocusableElements();

        const oldHide = changedProperties.get('hide');
        if (oldHide !== undefined) {
            if (!this.hide && oldHide !== false) {
                document.addEventListener('keydown', this._keyDownListener);
                //this._inner.addEventListener('keydown', this._keyDownListener);
                this._hide = !this._dispatchEvent(ModalEvent.Open);
                this._dragging = false;
            } else if (oldHide === false && this.hide) {
                document.removeEventListener('keydown', this._keyDownListener);
                //this._inner.addEventListener('keydown', this._keyDownListener);
                this._hide = this._dispatchEvent(ModalEvent.Close);
            }
        }
    }

    protected updated() {
        if (!this.hide && !this._rendered) {
            this._rendered = true;
            const animationKeyframes = Modal.animations['toggle'].show;
            const animationOptions = {
                duration: 150,
                iterations: 1,
            };
            const showAnimation = this._inner.animate
                ? this._inner.animate(animationKeyframes, animationOptions)
                : undefined;
            if (showAnimation) {
                showAnimation.onfinish = () => {};
            }
        }
    }

    protected firstUpdated(changedProperties) {
        super.firstUpdated(changedProperties);
        if (!this.hide) {
            document.addEventListener('keydown', this._keyDownListener);
            //this._inner.addEventListener('keydown', this._keyDownListener);
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            'no-overlay': this.noOverlay,
            padding: this.innerPadding,
            'click-close': this.clickOutsideClose,
            resizeable: this.resizeable,
            draggable: this.draggable,
            dragging: this._dragging,
            'fill-height': this.fillHeight,
            'fixed-width': this.fixedWidth,
            hide: this._hide,
            'no-scroll': this.noScroll,
        });

        // Could/should also have aria-labelledby="dialog-title" and some prop for title?!

        const slotHtml = html`<slot id="dialog-description"></slot>`;
        const scrollHtml = this.noScroll
            ? html`<div class="${baseClassName}-content">${slotHtml}</div>`
            : html`
            <${Modal.Scrollbar.tag} class="${baseClassName}-content" data-testid='modal-scrollbar'>
                ${slotHtml}
            </${Modal.Scrollbar.tag}>        
        `;
        return html`
                <${Modal.Overlay.tag}
                      class="${rootClasses}"
                      @mousedown="${this.clickOutsideClose ? this._closeClickOutside : null}"
                      @keydown="${this._rootKeydownListener}"
                      ?hide=${this._hide}
                      ?prevent-scroll=${ifDefined(this.noOverlay ? undefined : true)}
                      aria-modal="true"
                      role="dialog"
                      aria-describedby="dialog-description"
                      opacity='${this.noOverlay ? 0 : -1}'
                      data-testid='modal-overlay'
                >
                    <div class="${baseClassName}-inner ${baseClassName}-${
                        this.modalSize ? this.modalSize : ModalSize.Default
                    }">
                        <div class="${baseClassName}-top" 
                             @mousedown=${this.draggable ? (e) => this._dragMouseDown(e) : null}>
                            <${Modal.Button.tag} @click="${
                                this._closeByCloseBtn
                            }" class="${baseClassName}-close-button" type="tertiary" compact="true">
                               <${
                                   Modal.Icon.tag
                               } class="${baseClassName}-close-button-icon" slot="icon-slot" icon="${cancel}"></${
                                   Modal.Icon.tag
                               }>
                            </${Modal.Button.tag}>
                            <slot name=${ModalSlot.TOP_SLOT}></slot>
                        </div>
                        ${scrollHtml}
                        <div class="${baseClassName}-bottom">
                            <div class="${baseClassName}-bottom-left">
                                <slot name=${ModalSlot.BOTTOM_LEFT_SLOT}></slot>
                            </div>
                            <div class="${baseClassName}-bottom-center">
                                <slot name=${ModalSlot.BOTTOM_CENTER_SLOT}></slot>
                            </div>
                            <div class="${baseClassName}-bottom-right">
                                <slot name=${ModalSlot.BOTTOM_RIGHT_SLOT}></slot>
                            </div>
                        </div>
                    </div>
                </${Modal.Overlay.tag}>
            `;
    }

    private static _retrieveActiveElement(e) {
        return e.composedPath ? e.composedPath()[0] : e.path[0];
    }

    private _closeByCloseBtn() {
        this.hide = true;
    }

    private _closeClickOutside(e) {
        if (e.target === this._root) {
            e.preventDefault();
            this.hide = true;
        }
    }

    private _observeChildSubtree() {
        const observer = new MutationObserver(() => {
            this._resetFocusableElements();
        });
        observer.observe(this, { subtree: true, childList: true });
        return () => observer.disconnect();
    }

    private readonly _resetFocusableElements = () => {
        this._focusableElements = null;
    };

    private async _assertFocusableElementsComplete() {
        await this._assertFocusableElementsExist();
    }

    private async _assertFocusableElementsExist() {
        if (!this._focusableElements) {
            this._focusableElements = await focusableElements(this);
            if (this._focusableElements) {
                this._focusableElements.forEach((f) => (f.tabIndex = 0));
            }
        }
    }

    private _currentActiveIndex(e): number {
        let index = -1;
        if (this._focusableElements) {
            const active = Modal._retrieveActiveElement(e);
            index = this._focusableElements.indexOf(active);
        }
        return index;
    }

    private readonly _keyDownListener = async (e) => {
        await this._assertFocusableElementsComplete();

        if (e.keyCode === 9 && this.shadowRoot) {
            e.preventDefault();
            if (this._focusableElements && this._focusableElements.length > 0) {
                const activeIndex = this._currentActiveIndex(e);
                let nextIndex = e.shiftKey ? activeIndex - 1 : activeIndex + 1;
                if (nextIndex < 0) {
                    nextIndex = this._focusableElements.length - 1;
                } else if (nextIndex >= this._focusableElements.length) {
                    nextIndex = 0;
                }
                this._focusIndex(nextIndex);
            }
        } else if (e.keyCode === 27 && this.escapeClose) {
            this.hide = true;
        }
    };

    private _focusIndex(index) {
        if (this._focusableElements && index < this._focusableElements.length) {
            this._focusableElements[index].focus();
            if (this._focusableElements[index] instanceof HTMLInputElement) {
                (this._focusableElements[index] as HTMLInputElement).select();
            } else if (this._focusableElements[index] instanceof HTMLTextAreaElement) {
                (this._focusableElements[index] as HTMLTextAreaElement).select();
            }
            this._focusableElements[index].dispatchEvent(new Event('focus'));
        }
    }

    private _dragMouseDown(e) {
        if (e.target.className.indexOf('-close-button-icon') > 0) {
            return;
        }
        e.preventDefault();

        // get the mouse cursor position at startup:
        this._dragPos = { x: e.clientX, y: e.clientY };

        document.addEventListener('mousemove', this._elementDrag);
        document.addEventListener('mouseup', this._closeDragElement);
        this._dragging = true;
    }

    private _elementDrag(e) {
        e.preventDefault();
        // calculate the new cursor position:
        const pos1 = this._dragPos.x - e.clientX;
        const pos2 = this._dragPos.y - e.clientY;
        this._dragPos.x = e.clientX;
        this._dragPos.y = e.clientY;
        // set the element's new position:
        this._inner.style.top = this._inner.offsetTop - pos2 + 'px';
        this._inner.style.left = this._inner.offsetLeft - pos1 + 'px';
    }

    private _closeDragElement() {
        document.removeEventListener('mousemove', this._elementDrag);
        document.removeEventListener('mouseup', this._closeDragElement);
        this._dragging = false;
        this._dragPos = { x: 0, y: 0 };
    }

    /**
     * Dispatch event and returns outcome.
     * Boolean true means OK to proceed.
     * Boolean false means that outer eventHandler prevented default and blocked action!
     *
     * @param name
     * @private
     */
    private _dispatchEvent(name: ModalEvent) {
        return this.dispatchEvent(
            new CustomEvent(name, {
                cancelable: true,
                bubbles: true,
                composed: true,
            })
        );
    }

    private _rootKeydownListener(event) {
        event.stopPropagation();
    }
}
