import { css, cssSelector, cssVariableAsVar, doMediaquery, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

const cssNoMin = css; // html-minifier-terser fail!

export const modalStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const gray = cssVariableAsVar('color_secondary_4');

    return [
        style,
        cssNoMin`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                --overlay-position: fixed;
                --opacity: 0.4;
            }

            ${root} {
                --background-color: var(--color-secondary-1);
                --table-zebra-background: var(--color-secondary-2);
            }

            ${is('click-close')} {
                cursor: pointer;
            }
            ${is('!no-overlay')} {
            }

            ${is('compact')} {
                width: 40px;
                font-size: 1.5rem;
            }

            ${child('inner')} {
                position: absolute;
                width: 48%;
                height: auto;
                max-height: 84%;
                overflow: hidden;
                z-index: 1001;
                box-shadow: 0px 3px 6px #00000026;
                background-color: ${cssVariableAsVar('color_secondary_1')};
                border: 1px solid ${gray};
                cursor: default;
                display: flex;
                flex-direction: column;
                pointer-events: all;
            }

            ${is('hide')} ${child('inner')} {
                visibility: hidden;
                scale: 0.9;
                transition: scale 0.15s, visibility 0.15s;
            }

            ${is('!hide')} ${child('inner')} {
                visibility: visible;
                scale: unset;
                transition: scale 0.15s, visibility 0.15s;
            }

            ${is('dragging')} ${child('inner')} {
                opacity: 0.5;
                border: 2px solid black;
            }

            ${child('top')} {
                display: flex;
                flex-direction: row-reverse;
                justify-content: space-between;
                align-items: center;
                padding-right: 14px;
                border-bottom: 1px solid ${cssVariableAsVar('color_secondary_3')};
                font-size: 18px;
            }

            ${is('draggable')} ${child('top')}:hover {
                background-color: ${cssVariableAsVar('color_secondary_3')};
                cursor: move;
            }

            ${is('fill-height')} ${child('inner')} {
                height: 100%;
            }

            ${child('extra-small')} {
                width: 24%;
                max-width: 420px;
                max-height: 48%;
            }

            ${child('small')} {
                width: 36%;
                max-width: 576px;
                max-height: 72%;
            }

            ${child('default')} {
                width: 48%;
                max-width: 768px;
                max-height: 84%;
            }

            ${child('large')} {
                width: 72%;
                max-width: 1400px;
                max-height: 96%;
            }

            ${is('fixed-width')} ${child('extra-small')} {
                width: 420px !important;
            }
             ${is('fixed-width')} ${child('small')} {
                width: 576px !important;
            }
             ${is('fixed-width')} ${child('default')} {
                width: 768px !important;
            }
             ${is('fixed-width')} ${child('large')} {
                width: 1400px !important;
            }

            ${doMediaquery(
                'tablet',
                `
                ${child('extra-small')} {
                    width: 32%;
                }

                ${child('small')} {
                    width: 42%;
                }

                ${child('default')} {
                    width: 60%;
                }

                ${child('large')} {
                    width: 84%;
                }
                ${is('fixed-width')} ${child('extra-small')} {
                    width: 420px !important;
                }
                ${is('fixed-width')} ${child('small')} {
                    width: 576px !important;
                }
                ${is('fixed-width')} ${child('default')} {
                    width: 768px !important;
                }
                ${is('fixed-width')} ${child('large')} {
                    width: 1400px !important;
                }
            `
            )}

            ${doMediaquery(
                'mobile',
                `
                ${child('extra-small')} {
                    width: 48%;
                }

                ${child('small')} {
                    width: 60%;
                }

                ${child('default')} {
                    width: 84%;
                }

                ${child('large')} {
                    width: 96%;
                }
                ${is('fixed-width')} ${child('extra-small')} {
                    width: 420px !important;
                }
                ${is('fixed-width')} ${child('small')} {
                    width: 576px !important;
                }
                ${is('fixed-width')} ${child('default')} {
                    width: 768px !important;
                }
                ${is('fixed-width')} ${child('large')} {
                    width: 1400px !important;
                }
            `
            )}

            ${is('resizeable')} ${child('inner')} {
                resize: auto;
                overflow: hidden;
            }

            ${is('padding')} ${child('inner')} {
                padding: 16px;
            }

            ${child('content')} {
                flex: auto;
                overflow: hidden;
            }

            ${child('bottom')} {
                border-top: 1px solid ${cssVariableAsVar('color_secondary_3')};
                width: auto;
                height: auto;
                display: flex;
                justify-content: space-between;
                overflow: hidden;
                flex-shrink: 0;
            }
        `,
    ];
};
