import { BaseElement } from '@umetrics/sartorius-ui-base';

const _isDisabled = (e: HTMLElement): boolean => {
    const disabled = e.getAttribute('disabled');

    if (disabled === '') {
        return true;
    }
    if (disabled) {
        const lowerCase = disabled.toLowerCase();
        return !(lowerCase === 'false' || lowerCase === 'undefined');
    }
    return false;
};

const _isValid = (e: HTMLElement) => {
    const tag = e.tagName.toLowerCase();
    return (
        !_isDisabled(e) &&
        (['input', 'select', 'textarea', 'button'].indexOf(tag) !== -1 ||
            (['a', 'area'].indexOf(tag) !== -1 && e.getAttribute('href') !== null))
    );
};

const _getAllFocusableElements = async (element: HTMLElement, ret: HTMLElement[]) => {
    if (element.tagName.toLowerCase() === 'slot') {
        for (const e of (element as HTMLSlotElement).assignedElements() as HTMLElement[]) {
            await _getAllFocusableElements(e, ret);
        }
    } else if (_isValid(element)) {
        ret.push(element);
    } else {
        if (element.shadowRoot) {
            await (element as BaseElement).updateComplete;
        }
        const children = (element.shadowRoot ? element.shadowRoot : element).children;
        for (const child of Array.prototype.slice.call(children) as HTMLElement[]) {
            await _getAllFocusableElements(child, ret);
        }
    }
};

export const focusableElements = async (e: HTMLElement): Promise<HTMLElement[]> => {
    const ret: HTMLElement[] = [];
    await _getAllFocusableElements(e, ret);
    return ret;
};
