import { ModalSize } from './modalSize';

export enum ModalSlot {
    TOP_SLOT = 'top-slot',
    MAIN_SLOT = '', // Default
    BOTTOM_LEFT_SLOT = 'bottom-left-slot',
    BOTTOM_CENTER_SLOT = 'bottom-center-slot',
    BOTTOM_RIGHT_SLOT = 'bottom-right-slot',
}

export interface ModalProperties {
    hide?: boolean;
    noOverlay?: boolean;
    clickOutsideClose?: boolean;
    escapeClose?: boolean;
    innerPadding?: boolean;
    modalSize?: string | ModalSize;
    resizeable?: boolean;
    draggable?: boolean;
    noScroll?: boolean /* No internal scrollbar */;
    fixedWidth?: boolean;

    /* Emitted events, for reactify users */
    onModalOpen?: any;
    onModalClose?: any;
}
